<template>
    <div class="container">
        <div class="row">
            <div class="col-lg-12">
                <table id="list" class="table table-bordered">
                    <thead>
                        <tr>
                            <th>Title</th>
                            <th>Content</th>
                            <th>RegUser</th>
                            <th>RegDatetime</th>
                            <th>UpdateUser</th>
                            <th>UpdateDatetime</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="board in boardlist">
                            <td>{{board.title}}</td>
                            <td>{{board.content}}</td>
                            <td>{{board.reguser}}</td>
                            <td>{{board.regdatetime}}</td>
                            <td>{{board.updateuser}}</td>
                            <td>{{board.updatedatetime}}</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
</template>
<script>
import Constant from '../../Constant.js'
import {mapState} from 'vuex'

export default {
    name: 'boardList',
    computed: mapState(['boardlist']),
    mounted: function() {
        let page = 1;
        if (this.$route.query && this.$route.query.page) {
            page = parseInt(this.$route.query.page)
        }
        console.log(page);
        this.$store.dispatch(Constant.LIST_BOARD, {pageno: page});
    }
}
</script>



