import Vue from 'vue'
import Router from 'vue-router'
import BoardList from '../components/Board/List.vue'
import NotFound from '../components/NotFound.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/board/list'
    },
    {
      path: '/board/list',
      name: 'boardlist',
      component: BoardList
    },
    {
      path: '*', component: NotFound
    }
  ]
})



